import React from 'react'
import "./Navbar.css"

function Navbar({cartItems}) {
    return (
        <div className="navbar">
            <div>
            <a href="#/"> <h1>ShoppingWebsite</h1> </a>
            </div>
       <div>
       <a href="/">Cart</a> 
     <span> {cartItems ? cartItems.length: ""}</span> 
       <a href="/">Sign in</a>      
       </div>     
        </div>
    )
}

export default Navbar
