import React from "react";
import "./Main.css";

function Main({ products ,onAdd}) {
  return (
    <div className="main">
      <h1>Products</h1>
     
      <div className="itemsList">
        {products.map((product) => (
          <div id={product.id}>
            <img src={product.image} alt="im" />
            <div className="contents">
            {product.name}
           <p>{product.price}</p> 
            </div>
            <div className="butt">
            <button onClick={()=> onAdd(product)}>Add to cart</button>
            </div>
           
          </div>
        ))}
      </div>
    </div>
  );
}

export default Main;
